"use client";

import { createContext, useContext, useState, useEffect, ReactNode } from "react";
import { supabase } from "@/lib/supabaseClient";

interface User {
  id: string;
  name?: string;
  email: string;
  balance: number;
  nickname?: string;
}

interface AuthContextType {
  user: User | null;
  login: (userData: User) => void;
  logout: () => Promise<void>;
  addFunds: (amount: number) => Promise<void>;
  refreshBalance: (userId?: string) => Promise<void>;
  updateProfile: (updates: { name?: string; nickname?: string }) => Promise<void>;
  updateEmail: (newEmail: string) => Promise<void>;
  updatePassword: (newPassword: string) => Promise<void>;
}

const AuthContext = createContext<AuthContextType>({
  user: null,
  login: () => {},
  logout: async () => {},
  addFunds: async () => {},
  refreshBalance: async () => {},
  updateProfile: async () => {},
  updateEmail: async () => {},
  updatePassword: async () => {},
});

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | null>(null);
  const API_BASE = "http://localhost:4000/api/updateUser"; // ✅ Ajuste da URL

  // 🔑 Restaurar sessão
  useEffect(() => {
    const restoreSession = async () => {
      console.log("🔄 Tentando restaurar sessão Supabase...");
      const { data, error } = await supabase.auth.getSession();
      if (error) console.error("❌ Erro ao obter sessão:", error);

      if (data.session?.user) {
        console.log("✅ Sessão ativa encontrada:", data.session.user);
        await loadUserData(data.session.user.id);
      } else {
        const savedUser = localStorage.getItem("user");
        if (savedUser) {
          console.log("✅ Restaurando usuário do localStorage:", savedUser);
          setUser(JSON.parse(savedUser));
        }
      }
    };

    restoreSession();

    const { data: listener } = supabase.auth.onAuthStateChange((event, session) => {
      console.log("🔔 onAuthStateChange:", event, session);

      if (session?.user) {
        loadUserData(session.user.id);
      } else {
        setUser(null);
        localStorage.removeItem("user");
      }
    });

    return () => listener.subscription.unsubscribe();
  }, []);

  const loadUserData = async (userId: string) => {
    try {
      const { data: userData, error } = await supabase
        .from("users")
        .select("balance, nickname, name, email")
        .eq("id", userId)
        .single();

      if (error) throw error;

      const fullUser: User = {
        id: userId,
        email: userData.email ?? "",
        balance: userData.balance || 0,
        nickname: userData.nickname,
        name: userData.name,
      };

      console.log("✅ Usuário carregado:", fullUser);
      setUser(fullUser);
      localStorage.setItem("user", JSON.stringify(fullUser));
    } catch (err) {
      console.error("❌ Erro ao carregar dados do usuário:", err);
    }
  };

  const login = (userData: User) => {
    console.log("🔑 login chamado:", userData);
    setUser(userData);
    localStorage.setItem("user", JSON.stringify(userData));
  };

  const logout = async () => {
    console.log("🚪 logout chamado");
    await supabase.auth.signOut();
    setUser(null);
    localStorage.removeItem("user");
  };

  const addFunds = async (amount: number) => {
    if (!user?.id) return alert("Usuário não definido!");
    const newBalance = (user.balance || 0) + amount;

    const { error } = await supabase.from("users").update({ balance: newBalance }).eq("id", user.id);
    if (error) {
      console.error("❌ Erro ao adicionar fundos:", error);
      return alert("Erro ao adicionar fundos.");
    }

    const updatedUser = { ...user, balance: newBalance };
    setUser(updatedUser);
    localStorage.setItem("user", JSON.stringify(updatedUser));
    console.log("💰 Fundos adicionados:", updatedUser);
  };

  const refreshBalance = async (userId?: string) => {
    const id = userId || user?.id;
    if (!id) return;

    const { data, error } = await supabase.from("users").select("balance").eq("id", id).single();
    if (error) {
      console.error("❌ Erro ao atualizar saldo:", error);
      return;
    }

    if (data && typeof data.balance === "number") {
      const updatedUser = user ? { ...user, balance: data.balance } : null;
      setUser(updatedUser);
      if (updatedUser) localStorage.setItem("user", JSON.stringify(updatedUser));
      console.log("💵 Saldo atualizado:", updatedUser);
    }
  };

  const updateProfile = async (updates: { name?: string; nickname?: string }) => {
    if (!user?.id) return alert("Usuário não definido!");

    const { error } = await supabase.from("users").update(updates).eq("id", user.id);
    if (error) {
      console.error("❌ Erro ao atualizar perfil:", error);
      return alert("Erro ao atualizar perfil.");
    }

    const updatedUser = { ...user, ...updates };
    setUser(updatedUser);
    localStorage.setItem("user", JSON.stringify(updatedUser));
    console.log("✏️ Perfil atualizado:", updatedUser);
    alert("Perfil atualizado com sucesso!");
  };

  const updateEmail = async (newEmail: string) => {
    if (!user?.id) return alert("Usuário não definido");

    try {
      const res = await fetch(`${API_BASE}/email`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ userId: user.id, newEmail }),
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.error || "Erro ao atualizar email");

      setUser({ ...user, email: newEmail });
      localStorage.setItem("user", JSON.stringify({ ...user, email: newEmail }));
      console.log("📧 Email atualizado:", newEmail);
      alert(data.message);
    } catch (err: any) {
      console.error("❌ Erro ao atualizar email:", err);
      alert("Erro ao atualizar email: " + err.message);
    }
  };

const updatePassword = async (oldPassword: string, newPassword: string, confirmPassword: string) => {
  if (!user?.id) return alert("Usuário não definido");

  const res = await fetch("http://localhost:4000/api/updateUser/password", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      userId: user.id,
      oldPassword,
      newPassword,
      confirmPassword,
    }),
  });

  const data = await res.json();
  if (!res.ok) return alert("Erro: " + data.error);
  alert(data.message);
};

  return (
    <AuthContext.Provider
      value={{
        user,
        login,
        logout,
        addFunds,
        refreshBalance,
        updateProfile,
        updateEmail,
        updatePassword,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);
